.intro {
  width: 100%;
  height: 100vh;
  background-image: url("../img/intro_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 100%; }
.intro__wrap {
  padding-top: 240px;
  text-align: center;
  h1 {
    font-size: 90px;
    font-weight: 200;
    letter-spacing: 1px;
    color: #fff;
    animation: fade-up 1s .5s forwards 1; } }
.intro_text {
  padding-top: 35px;
  text-align: center;
  animation: fade-up 1.2s .6s forwards 1;
  p {
    color: #fff;
    font-weight: 200;
    word-spacing: 1px; } }
.intro_button {
  // position: relative
  padding-top: 70px;
  text-align: center;
  &:hover {
    transform: scale(1.3);
    transition: 1s; }

