.btn_down {
  height: 55px;
  width: 220px;
  border-radius: 25px;
  background-color: #25365d;
  border: 0;
  color: #fff;
  position: relative;
  animation: fade-visibility;
  animation-duration: 2s;
  animation-delay: .3s;
  a {
    color: #fff;
    text-decoration: none; } }


.img_save {
  vertical-align: middle;
  position: absolute;
  top: 50%;
  left: 40px;
  transform: translateY(-50%); }
